const InstaLink = (props) => {
  return(
    <div style={{marginTop: '8px',height: '25px', width: '25px'}}><a href={props.href} target="_blank"><img style={{height: '100%', width: '100%'}} src={instalogo}/></a></div>
  )
}

// Use `posed.div` elements anywhere on the pages.

const Transition = posed.div({
  preEnter: {
      x: '100%',
      transition: {
          duration: 600,
          ease: 'easeInOut'
          }
  },
  enter: {
    x: '0%',
    transition: {
      duration: 600,
      ease: 'easeInOut'
      }
  },
  exit: {
    x: '-100%',
    transition: {
      duration: 600,
      ease: 'easeInOut'
      }
  },
})

